.radio__text:after { position: absolute; content: ''; top: 10px; left: 22px; width: 8px; height: 8px; border-radius: 50%; background-color: #008e66; opacity: 0; visibility: hidden; transition: opacity 0.3s, visibility 0.3s; }

.input, .textarea { position: relative; }

.input input, .input textarea, .textarea input, .textarea textarea { padding: 12px; font-family: "Proxima Nova", Arial, sans-serif; font-size: 18px; font-weight: 400; line-height: 1.2; width: 100%; height: 62px; border-radius: 3px; border: 1px solid #999; transition: box-shadow .3s, border-color .3s; display: inline-block; }

.input input::placeholder, .input textarea::placeholder, .textarea input::placeholder, .textarea textarea::placeholder { color: #666; }

