.info__drop { position: absolute; top: 100%; left: 50%; color: #fff; margin-top: 22px; transform: translateX(-50%); width: 683px; min-height: 60px; padding: 8px 50px 8px 8px; font-size: 20px; font-weight: 400; line-height: 1.2; border-radius: 3px; z-index: 1; background-color: #078b66; opacity: 0; visibility: hidden; transition: opacity 0.3s, visibility 0.3s; }

@media only screen and (max-width: 1259px) { .info__drop { width: 500px; } }

.info__drop:before { position: absolute; content: ''; top: -12px; left: 50%; transform: translateX(-8px); width: 0px; height: 0px; border-style: solid; border-width: 0 8px 12px 8px; border-color: transparent transparent #078b66 transparent; }

.info.is-open .info__drop { opacity: 1; visibility: visible; }

