.aboutlist {
	margin-bottom: 48px;
	margin-left: -10px;
	flex-wrap: wrap;
	display: flex;
	@include sm {
		margin-left: 0;
		margin-bottom: 10px; }
	&__item {
		display: flex;
		align-items: center;
		@include f(18px,400,1.2);
		@include fs(25%);
		@include lg {
			font-size: 16px; }
		@include md {
			@include fs(50%);
			margin-bottom: 35px; }
		@include sm {
			font-size: 20px; }
		&:not(:last-child) {
			padding-right: 15px; } }

	&__icon {
		flex-shrink: 0;
		height: 57px;
		width: 57px;
		@include lg {
			width: 45px;
			height: 45px; }
		@include sm {
			height: 57px;
			width: 57px; }
		img {
			max-height: 100%;
			max-width: 100%;
			display: block; } }
	&__title {
		padding-left: 15px;
		flex-grow: 1;
		@include lg {
			padding-left: 10px; }
		@include sm {
			padding-left: 18px; } } }
