// GENERATE GRID EXAMPLE
// CUSTOM PADDING ON BREAKPOINTS - NOT INCLUDED IN MIXIN
// +generate_grid($columns: 3, $sizeXs: false, $sizeSm: false, $sizeMd: false, $sizeLg: true, $padding: 30px)
@include generate_grid();
.out {
	position: relative;
	overflow: hidden; }
.inner {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1216px; // 1196px
	@include md {
		padding: 0 25px; } }
.wrap {
	display: flex;
	align-items: flex-start; }
.sidebar {
	@include fs(386px);
	flex-shrink: 0;
	border-left: 1px solid #eee;
	border-right: 1px solid #eee;
	border-bottom: 1px solid #eee;
	background-color: #fdfefe;
	@include lg {
		@include fs(320px); }
	&__item {
		&:not(:last-child) {
			border-bottom: 1px solid #eee; }
		.soc {
			margin-top: 50px;
			@include lg {
				margin-top: 30px; } } }
	&__item-wrap {
		padding: 48px 38px 60px;
		@include lg {
			padding: 30px; } } }
.main {
	flex-grow: 1;
	// overflow: hidden
	&__wrap {
		padding: 44px 0 60px 48px;
		@include lg {
			padding: 40px 0 50px 30px; }
		@include md {
			padding-left: 0; }
		@include sm {
			padding: 0 0 34px; } } }
.section {
	position: relative;
	padding: 85px 0 116px;
	overflow: hidden;
	@include lg {
		padding: 65px 0 96px; }
	@include sm {
		padding: 40px 0 55px; }
	&_gray {
		background-color: #f8fafb; }
	&__inner {
		position: relative;
		z-index: 1; }
	&__img {
		position: absolute;
		top: -108px;
		left: -590px;
		max-width: 800px;
		z-index: -1;
		opacity: .3;
		img {
			max-width: 100%;
			display: block; } } }
.head {
	margin-bottom: 60px;
	&:not(:last-child) {
		margin-bottom: 60px;
		@include lg {
			margin-bottom: 45px; } }
	&:not(:first-child) {
		margin-top: 60px;
		@include lg {
			margin-top: 45px; } } }
.navlist {
	li {
		@include f(18px,600,1.2);
		@include lg {
			font-size: 16px; }
		&:not(:last-child) {
			margin-bottom: 20px; }
		&.is-active,
		&.active {
			a {
				color: #b03000; } } }
	a {
		transition: color .3s;
		color: #333;
		@include hover {
			color: #b03000; }
		&.is-active,
		&.active {
			color: #b03000; } } }

.md-hide {
	@include md {
		display: none !important; } }
.md-show {
	display: none !important;
	@include md {
		display: block !important; } }
