.article,
article {
	@include prevent-text;
	h1 {
		&:not(:last-child) {
			margin-bottom: 50px; }
		&:not(:first-child) {
			margin-top: 50px; } }
	h2 {
		position: relative;
		@include f(48px,600,1.2);
		@include lg {
			font-size: 42px; }
		@include sm {
			font-size: 48px;
			letter-spacing: 0.03em; }
		&:not(:last-child) {
			margin-bottom: 45px;
			@include lg {
				margin-bottom: 30px; }
			@include sm {
				margin-bottom: 43px; } }
		&:not(:first-child) {
			margin-top: 45px;
			@include lg {
				margin-top: 30px; }
			@include sm {
				margin-top: 43px; } }
		&:before {
			position: absolute;
			content: '';
			left: 0;
			top: 52px;
			height: 4px;
			width: 31px;
			background-color: #078b66;
			@include lg {
				top: 50px; }
			@include sm {
				top: 52px; } } }
	h3 {
		&:not(:last-child) {
			margin-bottom: 35px; }
		&:not(:first-child) {
			margin-top: 35px; } }
	h4 {
		&:not(:last-child) {
			margin-bottom: 25px; }
		&:not(:first-child) {
			margin-top: 25px; } }
	h5 {
		&:not(:last-child) {
			margin-bottom: 25px; }
		&:not(:first-child) {
			margin-top: 25px; } }
	.title {
		&:not(:last-child) {
			margin-bottom: 45px; }
		&:not(:first-child) {
			margin-top: 45px; } }
	p {
		letter-spacing: -0.015em;
		&:not(:last-child) {
			margin-bottom: 27px;
			@include sm {
				margin-bottom: 28px; } } }
	a:not([class]) {
		color: #b03000;
		border-bottom: 1px solid;
		transition: border-color .3s;
		@include hover {
			border-color: transparent; } }
	&__share {
		flex-wrap: wrap;
		align-items: center;
		display: flex;
		.btn {
			margin-right: 30px;
			@include sm {
				margin-right: 15px; } } }
	&__share-soc {
		font-size: 0;
		img {
			display: block;
			@include sm {
				max-width: 205px; } } } }
