.footer {
	padding: 60px 0 95px 24px;
	@include f(18px,400,1.4);
	overflow: hidden;
	background-color: #e4eaed;
	@include md {
		padding: 38px 0 60px;
		@include f(20px,400,1);
		background-color: #edf1f4; }
	&__inner {
		position: relative;
		&:before {
			position: absolute;
			top: 0;
			right: 0;
			content: '';
			top: -230px;
			right: -414px;
			width: 806px;
			height: 657px;
			opacity: .15;
			mix-blend-mode: multiply;
			background-size: contain;
			background-image: url('../img/proc.png');
			background-repeat: no-repeat;
			background-position: 0 0;
			@include md {
				display: none; } } }
	&__wrap {
		position: relative;
		z-index: 1;
		margin: 0 -15px;
		flex-wrap: wrap;
		display: flex;
		@include md {
			display: block; } }
	&__contacts,
	&__deductions,
	&__client,
	&__extra {
		padding: 0 15px; }
	&__contacts {
		@include fs(27.8%);
		@include md {
			position: relative;
			@include fs(100%); } }
	&__nav {
		margin: 0 -10px;
		min-width: 100%;
		border-top: 1px solid #e7e7e7;
		.nav {
			padding: 0 25px;
			margin-bottom: -1px;
			a {
				padding-left: 50px; } } }
	&__deductions {
		@include fs(24.3%); }
	&__client {
		@include fs(20.8%); }
	&__extra {
		@include fs(27.1%); }
	&__logo {
		margin-bottom: 42px;
		width: 212px;
		display: block;
		img {
			display: block;
			max-width: 100%; }
		@include md {
			margin: 0 50px 30px 0; } }
	&__trigger {
		position: absolute;
		top: 9px;
		right: 13px;
		background-color: transparent;
		@include rotate(90);
		transition: transform .3s;
		[data-accordion-open].is-open & {
			@include rotate(-90); }
		.icon {
			font-size: 13px; } }
	&__item {
		@include md {
			padding-left: 50px; }
		&:not(:last-child) {
			margin-bottom: 36px;
			@include md {
				margin-bottom: 25px; } }
		p {
			&:not(:last-child) {
				margin-bottom: 15px; } } }
	&__phone {
		@include f(24px,400,1.4);
		color: #b03000;
		border-bottom: 1px solid transparent;
		transition: border-color .3s;
		@include hover {
			border-color: #b03000; } }
	&__title {
		margin-bottom: 42px;
		@include f(24px,600,1.6);
		@include lg {
			line-height: 1.3; } }
	&__list {
		line-height: 1.3;
		li {
			&:not(:last-child) {
				margin-bottom: 21px; } }
		a {
			color: #333;
			transition: color .3s;
			@include hover {
				color: #b03000; } } } }

.copy {
	@include md {
		margin: 0 -10px;
		padding-top: 38px;
		border-top: 1px solid #e7e7e7;
		text-align: center;
		@include f(24px,700,1.2); } }
