.linkitem {
	display: flex;
	align-items: center;
	@include f(18px,600,1.1);
	color: #000;
	transition: color .3s;
	@include lg {
		font-size: 16px; }
	@include md {
		font-size: 20px;
		text-transform: uppercase; }
	@include hover {
		color: #b03000; }
	&:not(:last-child) {
		margin-bottom: 36px;
		@include lg {
			margin-bottom: 20px; } }
	&__icon {
		flex-shrink: 0;
		border-radius: 8px;
		background-color: #fff;
		box-shadow: 0px 8px 24px 0px rgba(173, 189, 184, 0.4);
		width: 63px;
		height: 60px;
		line-height: 60px;
		text-align: center;
		.icon {
			font-size: 35px;
			vertical-align: middle; } }
	&__title {
		padding-left: 14px;
		flex-grow: 1; } }
