html.is-overflow-hidden {
	overflow-y: scroll; }

body {
	@include fpn;
	@include f(20px, 300, 1.35);
	color: #363636;
	min-width: 500px;
	background: #FFF;
	@include lg {
		@include f(16px, 300, 1.30); }
	@include sm {
		@include f(20px, 300, 1.35); }
	.is-open-menu &,
	.is-overflow-hidden & {
		width: 100%; } }

.title {
	position: relative;
	@include f(48px,600,1.2);
	@include lg {
		font-size: 42px; }
	@include sm {
		font-size: 48px; }
	&:before {
		position: absolute;
		content: '';
		left: 0;
		top: 52px;
		height: 4px;
		width: 31px;
		background-color: #078b66;
		@include lg {
			top: 50px; } } }
