.total {
	@include f(24px,300,1);
	@include lg {
		font-size: 20px; }
	@include sm {
		font-size: 24px;
		letter-spacing: -0.01em; }
	&:not(:first-child) {
		margin-top: 50px; }
	&__col {
		@include sm {
			display: flex;
			justify-content: space-between; }
		&:not(:last-child) {
			@include sm {
				margin-bottom: 15px; } }
		&:not(:first-child) {
			@include sm {
				.total__numb {
					font-size: 24px; } } } }
	&__title {
		min-height: 47px;
		@include f(40px,300,1);
		@include lg {
			min-height: 40px; }
		@include sm {
			min-height: 0;
			padding-right: 10px; } }
	&__numb {
		margin-top: 20px;
		@include f(40px,400,1);
		color: #078b66;
		@include sm {
			margin-top: 0;
			min-width: 100px;
			text-align: right; } }
	&__description {
		margin-top: 28px;
		@include sm {
			margin-top: 15px; } }
	&__text {
		@include sm {
			padding-right: 10px;
			br {
				display: none; } } } }
