.order {
	position: relative;
	padding-bottom: 16px;
	overflow: hidden;
	color: #fff;
	&__wrap {
		display: flex;
		@include md {
			flex-direction: column; } }
	&__out {
		padding-top: 64px;
		position: relative;
		border-radius: 8px 0 0 8px;
		background-color: #05936b;
		@include md {
			border-radius: 0; } }
	&__form {
		margin-bottom: -16px;
		flex-shrink: 0;
		@include md {
			margin: 0 -25px; }
		.form {
			@include md {
				min-width: 100%;
				width: auto;
				border-radius: 0; } } }
	&__content {
		position: relative;
		padding: 34px 0 0 140px;
		z-index: 1;
		@include lg {
			padding: 34px 0 0 70px; }
		@include md {
			padding: 34px 0;
			order: -1; }
		@include sm {
			padding: 0 0 60px; }
		&:before {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			content: '3-НДФЛ';
			@include f(856px,700,1);
			white-space: nowrap;
			pointer-events: none;
			color: rgba(255, 255, 255, 0.102); } }
	&__title {
		@include f(36px,400,1.2);
		margin-bottom: 95px;
		@include md {
			margin-bottom: 54px;
			font-size: 34px; } }
	&__lenght {
		margin-left: -10px;
		margin-bottom: 20px;
		@include f(45px,600,1.1);
		text-transform: uppercase;
		@include md {
			margin-bottom: 30px; } }
	&__description {
		@include f(24px,400,1.1); } }
