	@include fontpath('Proxima Nova Light', 'ProximaNova-Light', ProximaNova-Light); }
@font-face {
	@include fontdefault(italic, 300, $proximaNova);
	@include fontpath('Proxima Nova Light Italic', 'ProximaNova-LightIt', ProximaNova-LightIt); }

@font-face {
	@include fontdefault(normal, 400, $proximaNova);
	@include fontpath('Proxima Nova Regular', 'ProximaNova-Regular', ProximaNova-Regular); }
@font-face {
	@include fontdefault(italic, 400, $proximaNova);
	@include fontpath('Proxima Nova Regular Italic', 'ProximaNova-RegularIt', ProximaNova-RegularIt); }

@font-face {
	@include fontdefault(normal, 600, $proximaNova);
	@include fontpath('Proxima Nova Semibold', 'ProximaNova-Semibold', ProximaNova-Semibold); }
@font-face {
	@include fontdefault(italic, 600, $proximaNova);
	@include fontpath('Proxima Nova Semibold Italic', 'ProximaNova-SemiboldIt', ProximaNova-SemiboldIt); }

@font-face {
	@include fontdefault(normal, 700, $proximaNova);
	@include fontpath('Proxima Nova Bold', 'ProximaNova-Bold', ProximaNova-Bold); }
@font-face {
	@include fontdefault(italic, 700, $proximaNova);
	@include fontpath('Proxima Nova Bold Italic', 'ProximaNova-BoldIt', ProximaNova-BoldIt); }
