.breadcrumbs-wrap {
	padding: 20px 0 20px 20px;
	border-bottom: 1px solid rgb(240, 245, 246);
	background-color: rgb(248, 250, 251);
	@include md {
		display: none; } }
.breadcrumbs {
	@include f(18px,600,1.38);
	flex-wrap: wrap;
	display: flex;
	@include lg {
		font-size: 16px; }
	&__item {
		position: relative;
		&:not(:last-child) {
			padding-right: 37px; }
		.icon {
			position: absolute;
			top: 7px;
			right: 13px;
			font-size: 12px;
			@include lg {
				font-size: 10px; } } }
	a {
		color: #333;
		transition: color .6s;
		@include hover {
			color: #ce3c06; } } }
